import * as React from 'react';
import { Typography, withStyles, WithStyles, createStyles, Theme } from "@material-ui/core";
import { TypographyProps } from '@material-ui/core/Typography';
import { Style } from 'jss/css';

const styles = (theme: Theme) => createStyles({
    body1: {
        lineHeight: "0.8em"
    },
    green: {
        color: theme.palette.type === "dark" ? "#64dd17" : "#64dd17"
    },
    yellow: {
        color: "#d59434"
    },
    red: {
        color: "#f44336"
    },
    white: {
        color: "#ffffff"
    },
    inherit: {
        color: "inherit"
    }
});

export type DotFontSize = 20 | 30 | 32 | 34 | 36 | 40 | 50 | 60 | 70 | 120;
type DotFontColor = "green" | "red" | "yellow" | "white" | "inherit";

interface IDotTypographyProps extends TypographyProps {
    dotFontSize?: DotFontSize;
    dotFontColor?: DotFontColor;
}

function dotTypography(props: IDotTypographyProps & WithStyles<typeof styles>) {
    const defaultProps: IDotTypographyProps = {
        dotFontSize: 40,
        dotFontColor: "inherit"
    }

    props = Object.assign(defaultProps, props);

    const classNames = [props.classes.body1, props.classes[props.dotFontColor || "inherit"], "dotFont"].join(" ");

    return <Typography variant="body1" className={classNames} style={{ fontSize: props.dotFontSize, ...props.style }} >{props.children}</Typography>
}
export const DotFontTypography = withStyles(styles)(dotTypography);


