import * as React from 'react';
import { Icon, Button, CircularProgress, Theme, createStyles, withStyles, WithStyles, Dialog, DialogTitle, DialogContent, DialogContentText, TextField, DialogActions } from "@material-ui/core";
import { AnswerQuestion } from "../state-management/Store";
import store from "../state-management/Store";
import { QrCodeReader } from "../QrCodeReader";
import { observer } from "mobx-react";

const styles = (theme: Theme) => createStyles({
    codeInput: {
        textTransform: "uppercase"
    }
});

interface ITakeQrImageProps extends WithStyles<typeof styles> {
    questionId: number;
    wrongAnswer: boolean;
}


interface ITakeQrImageState {
    readingCode: boolean;
    readFailure: boolean;
    dialogOpen: boolean;
}

@observer
class TakeQrImage extends React.Component<ITakeQrImageProps, ITakeQrImageState> {
    private categoryCodeInput: HTMLInputElement | null;

    constructor(props: ITakeQrImageProps) {
        super(props);
        this.state = {
            readingCode: false,
            readFailure: false,
            dialogOpen: false
        }
        this.categoryCodeInput = null;
    }

    handleImageScan = (e: any) => {
        this.setState({ readingCode: true });
        this.readQrCode(e.target.files[0]);
        e.target.value = "";
    }

    readQrCode = (file: File) => {
        let qr = new QrCodeReader;

        qr.ReadQrCode(file).then(code => {
            if (code !== "") {
                console.log("handling answer");
                this.handleAnswerQuestion(code);
            }
            else {
                this.handleCodeReadFailure();
            }
        });
    }

    handleAnswerQuestion = (answerCode: string) => {
        AnswerQuestion(this.props.questionId, answerCode).then(x => {
            console.log(x);
            return;
        }).then(() => {
            this.setState({ readFailure: false, readingCode: false, dialogOpen: false })
            return;
        });
    }

    handleCodeReadFailure = () => {
        this.setState({ readFailure: true, dialogOpen: true });
    }

    loadData = () => {
        this.setState({})
        var openCam = document.getElementById("open-camera");
        if (openCam != null)
            openCam.click();
    }

    scanIcon = (): any => {
        if (this.state.readingCode === true) {
            return <CircularProgress />
        }
        
        return <Icon color={this.props.wrongAnswer ? "error" : "inherit"} fontSize="large">add_a_photo</Icon>
    }

    handleDialogCancel = () => {
        this.setState({ dialogOpen: false, readingCode: false, readFailure: false });
    }

    handleDialogSubmit = () => {
        this.setState({ dialogOpen: false }, () => {
            if (this.categoryCodeInput !== null) {
                this.handleAnswerQuestion(this.categoryCodeInput.value);
            }
        });
    }

    render() {
        const { classes } = this.props;
        return <>
            <Dialog open={this.state.dialogOpen} onClose={this.handleDialogCancel} aria-labelledby="form-dialog-title" >
                <DialogTitle id="form-dialog-title">Unable to read QR code</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        The system was unable to read a valid QR code. Close this window and try again, or enter the Answer Code.
                    </DialogContentText>
                    <TextField autoFocus margin="dense" id="categoryCode" label="Challenge Code" type="text" fullWidth inputRef={(x) => { this.categoryCodeInput = x }} inputProps={{ className: classes.codeInput }} />
                </DialogContent>
                <DialogActions>
                    <Button onClick={this.handleDialogCancel} color="primary">Cancel</Button>
                    <Button onClick={this.handleDialogSubmit} color="primary">Submit</Button>
                </DialogActions>
            </Dialog>
            <input type="file" accept="image/*" capture="camera" style={{ left: -1000, top: -1000, display: "none" }} id="open-camera" onChange={this.handleImageScan} />
            <Button variant="contained" color="secondary" style={{ height: 80, width: 80, zIndex: 10 }} onClick={this.loadData}>
                {this.scanIcon()}
            </Button>
        </>
    }
}

export default withStyles(styles)(TakeQrImage);


