import * as React from 'react';
import Leaderboard from "./Leaderboard";
import { observer } from 'mobx-react';
import store from "./state-management/Store";
import { intercept } from 'mobx';
import { propagateChangeConfirmed, createDecoratorForEnhancer } from 'mobx/lib/internal';
import { Typography, AppBar, Toolbar } from '@material-ui/core';
import { DotFontTypography } from './common/DotFontTypography';
import apiController from './api/ApiController';
import { AllImages } from './api/IApi';

interface State {
    scrolling: boolean;
    currentImageIndex: number;
    images: AllImages[];
}

@observer
class SinglePageLeaderboard extends React.Component<any, State> {

    constructor(props: any) {
        super(props);
        this.state = {
            scrolling: false,
            currentImageIndex: 1,
            images: []
        }
        this.showPictures();
    }

    scrollToBottom = (secondsPerScroll: number) => {
        const body = document.getElementById("topScroll") as Element;
        const startTime = window.performance.now();

        const millisecondsPerScroll = secondsPerScroll * 1000;
        const lerp = this.lerp;
        const restart = this.restartScroll;
        let scrollBottom = (body.scrollHeight - (body as any).offsetHeight) - 30;

        function scroll() {
            let now = window.performance.now();
            const time = Math.min(1, ((now - startTime) / millisecondsPerScroll));
            var lerpResult = lerp(0, scrollBottom, time);
            console.log("scroll", scrollBottom, lerpResult);
            body.scroll(0, lerpResult);

            if (body.scrollTop >= scrollBottom) {
                restart();
                return;
            }

            requestAnimationFrame(scroll);
        }

        scroll();
    }

    hidePictures = () => {
        const allImages = document.querySelectorAll("#ulImages li");

        for (let i = 0; i < allImages.length; i++) {
            allImages[i].classList.remove("show");
            allImages[i].classList.remove("show2");
        }

        setTimeout(() => {
            this.showPictures();
        }, 4200)
    }

    showPictures = () => {
        const allImages = document.querySelectorAll("#ulImages li");

        if (this.state.currentImageIndex > allImages.length) {
            this.setState({ currentImageIndex: 1 });
            this.loadMoreImages();
        }

        const current = document.querySelector(`#ulImages li:nth-child(${this.state.currentImageIndex})`);
        const next = document.querySelector(`#ulImages li:nth-child(${this.state.currentImageIndex + 1})`);

        if (current != null) {
            current.classList.add("show");
            current.classList.add("show2");
        }
        if (next != null) {
            next.classList.add("show");
        }
        console.log(current, next);
        let cur = this.state.currentImageIndex;
        cur += 2;
        this.setState({ currentImageIndex: cur });

        setTimeout(() => {
            this.hidePictures();
        }, 4200)
    }

    loadMoreImages = async () => {
        const images = await apiController.GetAllImages();
        this.setState({images})
    }

    restartScroll = () => {
        if (store.UserStanding !== undefined) {
            this.setState({ scrolling: true });
            var storeCount = store.UserStanding.Leaderboard.length;
            var time = storeCount < 30 ? storeCount / 2 : storeCount;
            setTimeout(() => {
                const body = document.getElementById("topScroll") as Element;
                body.scroll(0, 0);
                setTimeout(() => {
                    this.scrollToBottom(time);
                }, 3000);
            }, 3000);
        }
    }

    lerp(startInt: number, endInt: number, time: number) {
        return startInt + time * (endInt - startInt);
    }

    render() {
        if (!this.state.scrolling) {
            this.restartScroll();
        }

        return <>
            <AppBar square={true} style={{ cursor: "none" }} position="fixed" color="primary">
                <Toolbar>
                    <DotFontTypography style={{ paddingLeft: 40, paddingTop: 40 }} dotFontSize={120} dotFontColor="white">Leaderboard</DotFontTypography>
                </Toolbar>
            </AppBar>
            <div id="topScroll" style={{ marginTop: 140, height: "400px", overflowY: "hidden", cursor: "initial", flex: 1 }}>
                <Leaderboard headerFontSize={70} bodyFontSize={70} />
            </div>
            <div style={{ flex: 1, marginTop: 10,overflow:"unset" }} id="bottomScroll">
                <ul className="ul-images" id="ulImages">
                    {this.state.images.map((x: AllImages, i: number) => {
                        return <li key={i}><img src={"data:image/png;base64," + x.Base64Image} {...{ datauserid: x.UserId }} style={{maxHeight:400}} /></li>
                    })}
                </ul>
            </div>
        </>
    }

}

export default SinglePageLeaderboard;





