import * as React from 'react';
import { RouteComponentProps } from 'react-router';
import store from "../state-management/Store";
import { ICategory } from '../api/IApi';
import Questions from '../Categories/Questions';
import { observer } from 'mobx-react';
import { changeDependenciesStateTo0 } from 'mobx/lib/internal';

interface IRouteComponentProp
{
    categoryCode: string;
}

interface IChallengeProps extends RouteComponentProps<IRouteComponentProp>
{

}

@observer
class Challenge extends React.Component<IChallengeProps, any> {

    render()
    {
        const { categoryCode } = this.props.match.params;
        const { Challenges } = store;
        if (Challenges === undefined)
            return null;

        console.log("catids", Challenges.map(x=> x.CategoryId), categoryCode);
        const category = Challenges.find(x=> x.Code.toUpperCase() === categoryCode.toUpperCase()) as ICategory;
        console.log("challange qs", category.Questions);
        if (category === undefined)
            return null;

        return (
            <Questions {...this.props} qs={category.Questions} type={category.Type}
                isLocked={!category.IsUnlocked} deptName={category.Name} catId={category.CategoryId}
                img={category.BgImage} deptInformation={category.Description} />
        )
    }
}


export default Challenge;


