import * as React from 'react';
import { observer } from "mobx-react";
import { createStyles, withStyles, WithStyles, Paper, Icon, Theme, AppBar, Toolbar } from '@material-ui/core';
import { DotFontTypography } from "./common/DotFontTypography";
import store from "./state-management/Store";
import { IUserStanding } from './api/IApi';

declare var userId: string;

const styles = (theme: Theme) => createStyles({
    paper: {
        padding: 4,
        //alignItems: "center",
        justifyContent: 'space-around',
        display: "flex",
        backgroundColor: theme.palette.primary.dark//theme.palette.type === "dark" ? "#111" : theme.palette.primary.dark
    },
    middle:
    {
        display: "flex",
        flex: 1,
        justifyContent: "center",
        alignItems: "center"
    },
    iconGreen: {
        color: theme.palette.type === "dark" ? "#57a46c" : "#64dd17"
    },
    iconRed: {
        color: "#f44336"
    }
});

interface IMyRankingProps extends WithStyles<typeof styles> { }

@observer
class MyRanking extends React.Component<IMyRankingProps, any> {
    render() {
        var { UserStanding } = store;
        if (UserStanding === undefined)
            return null;

        return (
            <AppBar square={true} position="relative" className={this.props.classes.paper}>
                <Toolbar>
                    <DotFontTypography dotFontSize={60} dotFontColor="white">{UserStanding.Initials}</DotFontTypography>
                    <div className={this.props.classes.middle}>
                        <DotFontTypography dotFontColor="green" dotFontSize={40}>+${UserStanding.LastPointsAwarded.toLocaleString()}</DotFontTypography>
                        <Icon className={this.props.classes.iconGreen}>arrow_drop_up</Icon>
                    </div>
                    <DotFontTypography dotFontColor="green" dotFontSize={50}>${UserStanding.TotalPointsAwarded.toLocaleString()}</DotFontTypography>
                </Toolbar>
            </AppBar>
        )
    }
}

export default withStyles(styles)(MyRanking);



