import bgx from "./backgrounds/bg_sprites.jpg";
import bg0 from "./backgrounds/bg0.jpg";
import bg1 from "./backgrounds/bg1.jpg";
import bg2 from "./backgrounds/bg2.jpg";
import bg3 from "./backgrounds/bg3.jpg";
import bg4 from "./backgrounds/bg4.jpg";
import bg5 from "./backgrounds/bg5.jpg";
import bg6 from "./backgrounds/bg6.jpg";
import bg7 from "./backgrounds/bg7.jpg";
import bg8 from "./backgrounds/bg8.jpg";
import bg9 from "./backgrounds/bg9.jpg";
import bg10 from "./backgrounds/bg10.jpg";
import bg11 from "./backgrounds/bg11.jpg";
import bg12 from "./backgrounds/bg12.jpg";
import bg13 from "./backgrounds/bg13.jpg";
import bg14 from "./backgrounds/bg14.jpg";
import bg15 from "./backgrounds/bg15.jpg";
import bg16 from "./backgrounds/bg16.jpg";
declare let __webpack_public_path__: any;

export function getImage(img: string | undefined): any
{
    if (img === undefined)
        return __webpack_public_path__+ bg0;

    switch (img.toLowerCase())
    {
        case "bg0.jpg":
            return "#8731A8";
        case "bg1.jpg":
            return "#8731A8";
        case "bg2.jpg":
            return "#31A893";
        case "bg3.jpg":
            return "#308EA7";
        case "bg4.jpg":
            return "#3161A8";
        case "bg5.jpg":
            return "#4C31A8";
        case "bg6.jpg":
            return "#A231A1";
        case "bg7.jpg":
            return "#A83147";
        case "bg8.jpg":
            return "#A88131";
        case "bg9.jpg":
            return "#8CA731";
        case "bg10.jpg":
            return "#547E45";
        case "bg11.jpg":
            return "#7E7245";
        case "bg12.jpg":
            return "#7E4848";
        case "bg13.jpg":
            return "#71457E";
        case "bg14.jpg":
            return "#4A477E";
        case "bg15.jpg":
            return "#456E7E";
        case "bg16.jpg":
            return "#457D79";
        default:
            return __webpack_public_path__+ bg0;
    }
}




