import * as React from 'react';
import store from "./state-management/Store";
import { ICategory } from './api/IApi';
import { Grid, createStyles, WithStyles, withStyles, Paper } from '@material-ui/core';
import Category from './Categories/Category';
import { observer } from 'mobx-react';

const styles = createStyles({
    gridRow: {
        "&:nth-child(odd)": {
            backgroundColor: "#1f407a"
        }
    }
});

interface IChallengesProps extends WithStyles<typeof styles> {

}

@observer
class Challenges extends React.Component<IChallengesProps, any> {
    render() {
        const { Challenges } = store;
        const { classes } = this.props;
        if (Challenges === undefined)
            return null;

        return (
            <Paper>
                <Grid container style={{ marginBottom: 110 }}>
                    {Challenges.map((x: ICategory) => {
                        return <Category category={x} key={x.CategoryId} />
                    })}

                </Grid>
            </Paper>
        );
    }
}

export default withStyles(styles)(Challenges);


