import * as React from 'react';
import { IQuestion } from '../api/IApi';
import { ListItem, createStyles, WithStyles, withStyles, Icon, Theme, Card, Typography, IconButton, MenuList, MenuItem, ListItemIcon, ListItemText } from '@material-ui/core';
import Question from './Question';
import { getImage } from "../ImageHandler";
import { RouteComponentProps } from 'react-router';
import store from "../state-management/Store";
import BonusQuestion from './BonusQuestion';

const styles = (theme: Theme) => createStyles({
    locked: {
        backgroundColor: "transparent",
        zIndex: 140,
        display: "flex",
        justifyContent: 'center',
        alignItems: "center",
        color: "white",
    },
    card: {
        backgroundColor: theme.palette.background.paper,
        flex: 1,
        marginBottom: 12
    }
});

interface IQuestionsProps extends RouteComponentProps<any>, WithStyles<typeof styles> {
    qs: IQuestion[];
    deptName: string;
    deptInformation?: string;
    isLocked: boolean;
    img?: string;
    type: string;
    catId: number;
}

class Questions extends React.Component<IQuestionsProps, any> {
    goBack = () => {
        store.goToAction("/challenges");
    }

    render() {
        const { classes, deptName, deptInformation, img } = this.props;
        return (
            <div style={{ minHeight: 80, flexFlow: "column", marginBottom: 132 }}>
                <ListItem style={{
                    background: `${getImage(img)}`, backgroundSize: "auto 100%", minHeight: 120, marginBottom: 12,
                    flexFlow: "column", alignItems: "flex-start", justifyContent: "center",
                    padding: 0
                }}>
                    <div style={{ backgroundColor: "rgba(0,0,0,0.2)", width: "100%" }}>
                        <MenuList style={{ padding: 0 }}>
                            <MenuItem onClick={this.goBack} style={{ height: 15, paddingLeft: 4 }} dense={true} disableGutters={true}>
                                <ListItemIcon style={{ marginRight: 5 }} >
                                    <Icon fontSize="small" style={{ color: "rgba(255,255,255,0.5)" }}>keyboard_arrow_left</Icon>
                                </ListItemIcon>
                                <Typography style={{ padding: 0, color: "rgba(255,255,255,0.4)" }} >Back to Challenges</Typography>
                            </MenuItem>
                        </MenuList>
                    </div>
                    <div style={{ padding: 11, flex: 1, marginTop: 20 }}>
                        <Typography variant="h4" style={{ color: 'white' }}>
                            {deptName}
                        </Typography>
                        <Typography variant="subheading" style={{ color: "rgba(255,255,255,0.7)" }}>
                            {deptInformation}
                        </Typography>
                    </div>
                </ListItem>


                {this.props.qs !== undefined && this.props.qs.map((x: IQuestion) => {
                    return (this.props.type === "Bonus" ? <BonusQuestion key={x.QuestionId} q={x} isLocked={this.props.isLocked} catId={this.props.catId} /> :
                        <Question key={x.QuestionId} q={x} isLocked={this.props.isLocked} />
                    )
                })}

            </ div>
        )
    }
}


export default withStyles(styles)(Questions);


