import { History, createHashHistory } from "history";
import { IUserStanding, ILeaderboard, ICategory } from "../api/IApi";
import { observable, action, computed } from "mobx";
import apiController from "../api/ApiController";

const baseUrl = document.getElementsByTagName('base')[0].getAttribute('href')!;
export const history: History = createHashHistory({ basename: baseUrl });

class PrivateStore {
    @observable public UserStanding?: IUserStanding;
    @observable public UserId: string;
    @observable public ActiveMenuItem: string;
    @observable public ThemeType: "dark" | "light";
    @observable public ReadQrCodesOnDevice: boolean;
    @observable public ShowQr: boolean = true;
    constructor() {
        const themeType = localStorage.getItem("themeType") as "dark" | "light" | null;
        this.UserId = "";
        this.ActiveMenuItem = "Leaderboard";
        this.ThemeType = themeType || "light";
        this.ReadQrCodesOnDevice = true;
    }
}

class Store {
    private _privates: PrivateStore;

    constructor(privates: PrivateStore) {
        this._privates = privates;
        history.push(location.pathname);
    }

    @action goToAction(to: string) {
        if (to.toLowerCase().indexOf("leaderboard") > -1) {
            this._privates.ActiveMenuItem = "Leaderboard";
        }
        else if (to.toLowerCase().indexOf("challenge") > -1) {
            this._privates.ActiveMenuItem = "Challenges";
        }
        else if (to.toLowerCase().indexOf("map") > -1) {
            this._privates.ActiveMenuItem = "Map";
        }
        else if (to.toLowerCase().indexOf("selfie") > -1) {
            this._privates.ActiveMenuItem = "Selfie";
        }
        else {
            this._privates.ActiveMenuItem = "Info";
        }
        history.push(to);
    }

    @action toggleTheme = () => {
        if (this._privates.ThemeType === "dark")
            this._privates.ThemeType = "light";
        else
            this._privates.ThemeType = "dark";
        localStorage.setItem("themeType", this._privates.ThemeType);
    }

    @action showQR = (show: boolean) => {
        this._privates.ShowQr = show;
    }

    @action toggleReadQrCodesOnDevice = () => {
        if (this._privates.ReadQrCodesOnDevice === true)
            this._privates.ReadQrCodesOnDevice = false;
        else
            this._privates.ReadQrCodesOnDevice = true;
    }

    @action UploadImage = async (image: string) => {
        await apiController.UploadImage(this._privates.UserId, image);
    }

    @computed get ReadQrCodesOnDevice(): boolean {
        return this._privates.ReadQrCodesOnDevice;
    }

    @computed get ActiveMenuItem(): string {
        return this._privates.ActiveMenuItem;
    }

    @computed get ThemeType(): "dark" | "light" {
        return this._privates.ThemeType
    }

    @computed get UserStanding(): IUserStanding | undefined {
        if (this._privates.UserStanding === undefined)
            return undefined;

        console.log("user standings", this._privates.UserStanding);

        return this._privates.UserStanding;
    }

    @computed get Leaderboard(): ILeaderboard[] | undefined {
        if (this._privates.UserStanding === undefined)
            return undefined;

        console.log("got leaderboard", new Date(Date.now()), this._privates.UserStanding);

        return this._privates.UserStanding.Leaderboard;
    }

    @computed get Challenges(): ICategory[] | undefined {
        if (this._privates.UserStanding === undefined)
            return undefined;

        return this._privates.UserStanding.Categories;
    }

    @computed get ShowQRCode(): boolean {
        return this._privates.ShowQr;
    }

}

const privates = new PrivateStore();
const store = new Store(privates);

apiController.GetUserId().then(x => {
    privates.UserId = x;
    LoadUserStandingData();
});

setInterval(() => {
    LoadUserStandingData();
}, 30000);

export function LoadUserStandingData() {
    apiController.GetUserStanding(privates.UserId).then(x => {
        privates.UserStanding = x;
    });
}

export function AnswerQuestion(questionId: number, answer: string): Promise<IUserStanding> {
    return apiController.PostAnswer(privates.UserId, questionId, answer).then(x => {
        privates.UserStanding = x;
        return x;
    });
}

export function UnlockCategory(categoryCode: string): Promise<string> {
    return apiController.UnlockCategory(privates.UserId, categoryCode)
        .then(x => {
            privates.UserStanding = x;
            return categoryCode;
        })
        .catch(() => {
            throw new Error("Invalid QR code value");
        });
}

export default store;


