import { IImageData, ImageReader } from "./ImageReader";
import jsQR from "jsqr";
import apiController from "./api/ApiController"

export class QrCodeReader {
    get MAX_WIDTH() { return 350; }

    public ReadQrCode = (file: File): Promise<string> => {
        return ImageReader.ReadImageFromFile(file, this.MAX_WIDTH).then(image => {
            return this._readQrCodeWithDevice(image).then(x => { return x }).catch(() => { return this._readQrCodeWithServer(image) });
        })
    }

    public ReadQrCodeWithServer = (file: File): Promise<string> => {
        return ImageReader.ReadImageFromFile(file, this.MAX_WIDTH).then(image => {
            return this._readQrCodeWithServer(image);
        })
    }

    private _readQrCodeWithDevice = (image: IImageData): Promise<string> => {
        let img = image.ImageData;
        const code = jsQR(img.data, img.width, img.height);

        return new Promise((resolve, reject) => {
            if (code) {
                console.log(`Client read: "${code.data}"`);
                resolve(code.data);
            } else {
                console.log("Client unable to detect QR Code");
                reject();
            }
        });
    }

    private _readQrCodeWithServer = (image: IImageData): Promise<string> => {
        return apiController.ReadQrCode(image.ImageAsBase64).then(result => {
            console.log(`Server read: "${result}"`);
            if (result === "Read error") {
                console.log("Server unable to detect QR Code");
                return "";
            }
            return result;
        });
    }
}


