import * as React from 'react';
import { IQuestion, IAnswer } from '../api/IApi';
import { ListItem, createStyles, WithStyles, withStyles, ListItemText, Theme, Typography, Button, Icon, RadioGroup, FormControlLabel, Radio, FormHelperText, Card, CardHeader, CardActionArea, CardContent, CardActions } from '@material-ui/core';
import { DotFontTypography } from '../common/DotFontTypography';
import { AnswerQuestion } from "../state-management/Store";

const styles = (theme: Theme) => createStyles({
    questionBlur: {
        filter: "blur(6px)",
    },
    question: {
        marginBottom: 8,
        position: "relative"
    },
    questionOverlay: {
        backgroundColor: theme.palette.type === "dark" ? "rgba(0,0,0,0.3)" : "rgba(255,255,255,0.3)",
        width: "100%",
        height: "100%",
        position: "absolute",
        zIndex: 1,
        display: "flex",
        justifyContent: "center",
        alignItems: "center"
    },
    spaceBetweenAnswers: {
        paddingTop: 8,
        paddingBottom: 8
    },
    radioRoot: {
        color: theme.palette.text.primary
    },
    radioChecked: {},
    radioLabel: {
        color: theme.palette.text.primary
    },
    radioLabelWrong: {
        color: theme.palette.error.main,
    },
    points: {
        backgroundColor: theme.palette.secondary.light,
        color: theme.palette.secondary.contrastText,
        textAlign: "center",
        marginLeft: 8,
        marginRight: 8,
        marginTop: 8,
        borderRadius: 2
    }
});

interface IQuestionProps extends WithStyles<typeof styles> {
    isLocked: boolean;
    q: IQuestion;
}

interface IQuestionState {
    chosenAnswer: string;
    wrongAnswer: boolean;
    submittedAnswer: boolean;
    submitting: boolean;
}

class Question extends React.Component<IQuestionProps, IQuestionState>
{
    constructor(props: IQuestionProps) {
        super(props);
        this.state = {
            chosenAnswer: "",
            wrongAnswer: false,
            submittedAnswer: false,
            submitting: false
        }
    }

    componentWillReceiveProps(nextProps: IQuestionProps) {
        if (nextProps.q.AttemptsMade > 0 && nextProps.q.PointsAwarded === 0) {
            this.setState({ wrongAnswer: true });
        } else {
            this.setState({ wrongAnswer: false })
        }
    }

    componentDidMount() {
        if (this.props.q.CorrectAnswer !== "?") {
            this.setState({ chosenAnswer: this.props.q.CorrectAnswer });
        }
    }

    chooseAnswer = (answer: string) => {
        this.setState({ chosenAnswer: answer, wrongAnswer: false, submittedAnswer: false });
    }

    submitAnswer = (questionId: number) => {
        this.setState({ submitting: true });
        AnswerQuestion(questionId, this.state.chosenAnswer).then(() => {
            this.setState({ submittedAnswer: true, submitting: false });
        });
    }

    render() {
        console.log("q", this.props);
        const { classes } = this.props;

        return (
            <Card className={classes.question}>
                {this.props.isLocked ? <div className={classes.questionOverlay}><Icon style={{ fontSize: "4em" }} color="action">lock</Icon></div> : null}
                <div className={this.props.isLocked ? classes.questionBlur : ""}>
                    <div className={classes.points} >
                        <DotFontTypography dotFontSize={30}>{this.props.q.PointsAwarded > 0 ? `${this.props.q.PointsAwarded.toLocaleString()}/` : ""}{this.props.q.PointValue.toLocaleString()}</DotFontTypography>
                    </div>
                    <CardHeader title={this.props.q.Text} />

                    <CardContent>
                        <RadioGroup value={this.state.chosenAnswer} onChange={(e: any) => { this.chooseAnswer(e.target.value) }} >
                            {this.props.q.Answers.filter(x => x.Value === this.props.q.CorrectAnswer || this.props.q.CorrectAnswer === "?").map((x: IAnswer, i: number) => {
                                if (this.state.wrongAnswer && this.state.chosenAnswer === x.Value && this.state.submittedAnswer) {
                                    return <FormControlLabel classes={{ label: classes.radioLabelWrong }} value={x.Value} label={x.Value} control={<Radio checkedIcon={<Icon color="error">highlight_off</Icon>} classes={{ root: classes.radioRoot, checked: classes.radioChecked }} color="primary" />} />
                                }
                                else {
                                    return <FormControlLabel classes={{ label: classes.radioLabel }} value={x.Value} label={x.Value} control={<Radio classes={{ root: classes.radioRoot, checked: classes.radioChecked }} color="primary" />} />
                                }
                            })}
                        </RadioGroup>
                    </CardContent>
                    <CardActions>
                        {!this.props.isLocked && this.props.q.PointsAwarded === 0 &&
                            <div style={{ width: "100%", display: "flex", justifyContent: "flex-start", paddingBottom: 8 }}>
                                <Button disabled={this.state.submitting} onClick={() => { this.submitAnswer(this.props.q.QuestionId) }} variant="contained" color="primary">Submit</Button>
                            </div>}
                    </CardActions>
                </div>
            </Card>
        )
    }
}

export default withStyles(styles)(Question);

