import { IApi, ILeaderboard, IUserStanding, AllImages } from "../IApi";

class Api implements IApi {
    GetAllImages(): Promise<AllImages[]> {
        return fetch(`http://webappdev_prod5/openhouse19/api/v1/getallimages`, {
            method: "get",
            mode: "cors",
            headers: {
                'Content-Type': 'application/json',
            }
        }).then(x => {
            return x.json();
        }).then(x => {
            //console.log(x);
            return x;
            });
    }

    UploadImage(userId: string, imageAsBase64: string): Promise<boolean> {
        var body = { userId, base64Image: imageAsBase64 };

        return fetch(`http://webappdev_prod5/openhouse19/api/v1/uploadimage`, {
            body: JSON.stringify(body),
            method: "POST",
            mode: "cors",
            headers: {
                'Content-Type': 'application/json',
            }
        }).then(x => {
            return x.json();
        }).then(x => {
            //console.log(x);
            return x;
        });
    }


    UnlockCategory(userId: string, categoryCode: string): Promise<IUserStanding> {
        var body = { CategoryCode: categoryCode };

        return fetch(`http://webappdev_prod5/openhouse19/api/v1/users/${userId}/categories`, {
            body: JSON.stringify(body),
            method: "POST",
            mode: "cors",
            headers: {
                'Content-Type': 'application/json',
            }
        }).then(x => {
            return x.json();
        }).then(x => {
            //console.log(x);
            return x;
        });
    }

    GetUserId(): Promise<string> {
        return new Promise((resolve) => {
            resolve("0051B00000CUJjSQAX");
        });
    }

    PostAnswer(userId: string, questionId: number, answer: string): Promise<IUserStanding> {
        var body = { QuestionId: questionId, Value: answer };

        console.log("body", body);
        return fetch(`http://webappdev_prod5/openhouse19/api/v1/users/${userId}/questions`, {
            body: JSON.stringify(body),
            method: "POST",
            mode: "cors",
            headers: {
                'Content-Type': 'application/json',
            }
        }).then(x => {
            return x.json();
        }).then(x => {
            //console.log(x);
            return x;
        });
    }

    GetUserStanding(userId: string): Promise<IUserStanding> {
        return fetch(`http://webappdev_prod5/openhouse19/api/v1/users/${userId}`).then(x => {
            return x.json();
        }).then(x => {
            return x;
        });
    }

    ReadQrCode(imageAsBase64: string): Promise<string> {
        var body = { ImageAsBase64: imageAsBase64 };

        return fetch(`http://webappdev_prod5/openhouse19/api/v1/qrcodes/read`, {
            body: JSON.stringify(body),
            method: "POST",
            mode: "cors",
            headers: {
                'Content-Type': 'application/json',
            }
        }).then(x => {
            return x.json();
        }).then(x => {
            //console.log(x);
            return x;
        });
    }

}

export default Api;


