import * as React from 'react';
import { Route, RouteProps, RouteComponentProps } from 'react-router-dom';
import Info from "./components/Info";
import Layout from "./components/Layout";
import { CssBaseline } from '@material-ui/core';
import Leaderboard from './components/Leaderboard';
import Challenges from './components/Challenges';
import Challenge from './components/Challenge/Challenge';
import Map from "./components/Map/Map";
import Selfie from "./components/Selfie";
import SinglePageLeaderboard from './components/SinglePageLeaderboard';

export const routes =
    <Layout {...({} as RouteComponentProps<any>)}>
        <CssBaseline />
        <Route exact path='/info' render={(props: any) => <Info {...props} />} />
        <Route exact path="/leaderboard" render={(props: any) => <Leaderboard {...props} />} />
        <Route exact path="/challenges" render={(props: any) => <Challenges {...props} />} />
        <Route exact path="/challenge/:categoryCode" render={(props: any) => <Challenge {...props} />} />
        <Route exact path="/map" render={(props: any) => <Map />} />
        <Route exact path="/selfie" render={(props: any) => <Selfie />} />
        <Route exact path="/singlepageleaderboard" render={(props: any) => <SinglePageLeaderboard />} />
    </Layout>;






