import * as React from "react";
import MyRanking from "./MyRanking";
import FooterMenu from "./Footer/FooterMenu";
import store from "./state-management/Store";
import { createStyles, WithStyles, withStyles, Theme } from "@material-ui/core";
import { RouteComponentProps, withRouter } from "react-router";
import { observer } from "mobx-react";
import { createMuiTheme, MuiThemeProvider } from '@material-ui/core/styles';
import { blue, grey, red, purple, green, teal, deepOrange, brown, deepPurple, indigo, pink } from '@material-ui/core/colors';

const theme = createMuiTheme({
    palette: {
        type: "dark",
        primary: indigo,
        secondary: blue,
        //contrastThreshold: 3,
        //tonalOffset: 0.4
    }
});

const themeLight = createMuiTheme({
    palette: {
        background: {
            paper: "#fff",
            default: "#ccc"
        },
        type: "light",
        primary: theme.palette.primary,
        secondary: theme.palette.secondary,
    }
});

const styles = (theme: Theme) => createStyles({
    root: {
        display: "flex",
        flexFlow: "column",
        height: "100vh",
    },
    body: {
        flex: 1,
        padding: 6,
        overflowX: "auto",
        WebkitOverflowScrolling: "touch"
    }
});

interface ILayoutProps extends WithStyles<typeof styles>, RouteComponentProps<any> { }

@observer
class Layout extends React.Component<ILayoutProps, any> {

    componentWillMount()
    {
        console.log(this.props);
    }

    render() {
        return (
            <MuiThemeProvider theme={store.ThemeType === "dark" ? theme : themeLight}>
                <div className={this.props.classes.root}>
                    <MyRanking />
                    <div className={this.props.classes.body}>{this.props.children}</div>
                    <FooterMenu {...({} as RouteComponentProps<any>)} />
                </div>
            </MuiThemeProvider>
        )
    }

}

const thisWithRouter = withRouter(Layout);
export default withStyles(styles)(thisWithRouter);


