import * as React from 'react';
import { Icon, Button, CircularProgress, Theme, createStyles, withStyles, WithStyles, Dialog, DialogTitle, DialogContent, DialogContentText, TextField, DialogActions } from "@material-ui/core";
import { StyledBottomNavigation, StyledBottomNavigationAction } from "./CustomBottomNavigation";
import { UnlockCategory } from "../state-management/Store";
import store from "../state-management/Store";
import { QrCodeReader } from "../QrCodeReader";
import { observer } from "mobx-react";

const styles = (theme: Theme) => createStyles({
    progress: {
        margin: theme.spacing.unit * 2,
    },
    codeInput: {
        textTransform: "uppercase"
    }
});

interface IFooterMenuProps extends WithStyles<typeof styles> {
}


interface IFooterMenuState {
    readingCode: boolean;
    readFailure: boolean;
    dialogOpen: boolean;
}

@observer
class FooterMenu extends React.Component<IFooterMenuProps, IFooterMenuState> {
    private categoryCodeInput: HTMLInputElement | null;

    constructor(props: IFooterMenuProps) {
        super(props);
        this.state = {
            readingCode: false,
            readFailure: false,
            dialogOpen: false
        }
        this.categoryCodeInput = null;
    }

    handleImageScan = (e: any) => {
        this.setState({ readingCode: true });
        //setTimeout(() => { this.setState({ readingCode: false }) }, 3000);
        this.readQrCode(e.target.files[0]);
        e.target.value = "";
    }

    readQrCode = (file: File) => {
        let qr = new QrCodeReader;

        qr.ReadQrCode(file).then(code => {
            if (code !== "") {
                this.handleUnlockCategory(code);
            }
            else {
                this.handleCodeReadFailure();
            }
        });
    }

    handleUnlockCategory = (categoryCode: string) => {
        UnlockCategory(categoryCode)
            .then(x => {
                store.goToAction(`/challenge/${x}`);
                return;
            })
            .then(() => {
                this.setState({ readingCode: false, readFailure: false, dialogOpen: false });
                return;
            })
            .catch(() => {
                this.handleCodeReadFailure();
            })
    }

    handleCodeReadFailure = () => {
        this.setState({ readFailure: true, dialogOpen: true });
    }

    loadData = () => {
        var openCam = document.getElementById("open-camera");
        if (openCam != null)
            openCam.click();
    }

    scanIcon = (): any => {
        if (this.state.readingCode === true) {
            const { classes } = this.props;
            return <CircularProgress className={classes.progress} />
        }
        return <Icon fontSize="large">add_a_photo</Icon>
    }

    handleDialogCancel = () => {
        this.setState({ dialogOpen: false, readingCode: false, readFailure: false });
    }

    handleDialogSubmit = () => {
        this.setState({ dialogOpen: false }, () => {
            if (this.categoryCodeInput !== null) {
                this.handleUnlockCategory(this.categoryCodeInput.value);
            }
        });
    }

    render() {
        const { classes } = this.props;
        return <>
            <Dialog open={this.state.dialogOpen} onClose={this.handleDialogCancel} aria-labelledby="form-dialog-title" >
                <DialogTitle id="form-dialog-title">Unable to read QR code</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        The system was unable to read a valid QR code. Close this window and try again, or enter the 3-letter Challenge Code.
                    </DialogContentText>
                    <TextField autoFocus margin="dense" id="categoryCode" label="Challenge Code" type="text" fullWidth inputRef={(x) => { this.categoryCodeInput = x }} inputProps={{ className: classes.codeInput }} />
                </DialogContent>
                <DialogActions>
                    <Button onClick={this.handleDialogCancel} color="primary">Cancel</Button>
                    <Button onClick={this.handleDialogSubmit} color="primary">Submit</Button>
                </DialogActions>
            </Dialog> {
                store.ShowQRCode &&
                <div style={{ position: "absolute", bottom: 68, right: 12 }}>
                    <input type="file" accept="image/*" capture="camera" style={{ left: -1000, top: -1000, display: "none" }} id="open-camera" onChange={this.handleImageScan} />
                    <Button variant="fab" color="secondary" style={{ height: 80, width: 80, zIndex: 10 }} onClick={this.loadData}>
                        {this.scanIcon()}
                    </Button>
                </div>}
            <StyledBottomNavigation value={store.ActiveMenuItem} >
                <StyledBottomNavigationAction to="/leaderboard" label="Leaderboard" value="Leaderboard" icon={<Icon>trending_up</Icon>} />
                <StyledBottomNavigationAction to="/challenges" label="Challenges" value="Challenges" icon={<Icon>verified_user</Icon>} />
                <StyledBottomNavigationAction to="/map" label="Map" value="Map" icon={<Icon>location_on</Icon>} />
                <StyledBottomNavigationAction to="/selfie" label="Selfie" value="Selfie" icon={<Icon>camera_front</Icon>} />
                <StyledBottomNavigationAction to="/info" label="Info" value="Info" icon={<Icon>info</Icon>} />
            </StyledBottomNavigation></>
    }
}

export default withStyles(styles)(FooterMenu);


