import * as React from 'react';
import store from "./state-management/Store";
import { Typography, Button, Icon, CircularProgress, DialogTitle, Dialog, DialogContent, DialogActions, IconButton } from '@material-ui/core';
import { IImageData, ImageReader } from "./ImageReader";
import apiController from "./api/ApiController";

interface ISelfie {
    readingCode: boolean;
    dialogOpen: boolean;
    imageBase64: string;
    uploading: boolean;
}

class Selfie extends React.Component<any, ISelfie>{
    constructor(props: any) {
        super(props);
        this.state = {
            readingCode: false,
            dialogOpen: false,
            imageBase64: "",
            uploading: false
        }
    }

    componentWillMount() {
        store.showQR(false);
    }

    componentWillUnmount() {
        store.showQR(true);
    }
    loadData = () => {
        this.setState({})
        var openCam = document.getElementById("open-camera");
        if (openCam != null)
            openCam.click();
    }

    scanIcon = (): any => {
        if (this.state.readingCode === true) {
            return <CircularProgress />
        }

        return <Icon color="inherit" fontSize="large">face</Icon>
    }

    getImage = async (file: File) => {
        const img = await ImageReader.ReadImageFromFile(file, 500);
        this.setState({ dialogOpen: true, imageBase64: img.ImageAsBase64 });
    }

    handleDialogCancel = () => {
        this.setState({ dialogOpen: false, readingCode: false, uploading: false });
    }

    handleDialogSubmit = async () => {
        this.setState({ uploading: true });
        await store.UploadImage(this.state.imageBase64);
        this.setState({ dialogOpen: false, readingCode: false, imageBase64: "", uploading: false });
    }

    handleImageScan = (e: any) => {
        this.setState({ readingCode: true });
        this.getImage(e.target.files[0]);
        e.target.value = "";
    }

    render() {
        return <>
            <Dialog open={this.state.dialogOpen} onClose={this.handleDialogCancel} aria-labelledby="form-dialog-title" >
                <DialogTitle id="form-dialog-title">Upload this picture?</DialogTitle>
                <DialogContent>
                    <img src={"data:image/png;base64," + this.state.imageBase64} />
                </DialogContent>
                <DialogActions>
                    <Button onClick={this.handleDialogCancel} color="primary">Cancel</Button>
                    <Button disabled={this.state.uploading} onClick={this.handleDialogSubmit} color="primary">Upload</Button>
                </DialogActions>
            </Dialog>

            <div style={{ display: "flex", justifyItems: "center", alignItems: "center", flexFlow: "column", marginTop: 20 }}>
                <input type="file" accept="image/*" capture="camera" style={{ left: -1000, top: -1000, display: "none" }} id="open-camera" onChange={this.handleImageScan} />
                <Button variant="contained" color="secondary" style={{ zIndex: 10, display: "block" }} onClick={this.loadData}>
                    <Typography variant="display1">
                        Take a selfie!
                    </Typography>
                    <Typography variant="body1">
                        You can find your images in the break room monitor!
                        </Typography>
                    <Typography>
                        {this.scanIcon()}
                    </Typography>
                </Button>
            </div>
        </>
    }
}


export default Selfie;


