import * as React from 'react';
import { ICategory, IQuestion } from '../api/IApi';
import { Grid, Collapse, createStyles, withStyles, WithStyles, ListItem, ListItemText, ListItemIcon, IconButton, Icon, Theme, Button, Divider, Avatar } from '@material-ui/core';
import store from "../state-management/Store";
import { getImage } from "../ImageHandler";

const styles = (theme: Theme) => createStyles({
    icon: {
        transform: 'rotate(0deg)',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    iconOpen: {
        transform: "rotate(180deg)"
    },
    //primaryWhite: {
    //    color: "#d59434"
    //},
    //gridItem: {
    //    "&:nth-child(odd)": {
    //        backgroundColor: "#1f407a"
    //    }
    //}

});

interface ICategoryProps extends WithStyles<typeof styles> {
    category: ICategory;
}

interface ICategoryState {
    isOpen: boolean;
}

class Category extends React.Component<ICategoryProps, ICategoryState> {
    constructor(props: any) {
        super(props);
        this.state = {
            isOpen: false
        }
    }

    toggleCollapse = () => {
        this.setState({ isOpen: !this.state.isOpen });
    }

    drawStars = () => {
        const { category } = this.props;
        let answeredQuestions = category.Questions.filter(x => x.CorrectAnswer !== '?').length;
        let totalCount = category.Questions.length;
        const icons: any[] = [];

        for (let i = 0; i < totalCount; i++) {
            if (i < answeredQuestions) {
                icons.push(<Icon key={`${category.CategoryId}_${i}_small`} color="action">star</Icon>)
            } else {
                icons.push(<Icon key={`${category.CategoryId}_${i}_star`} color="action">star_border</Icon>)
            }
        }

        return <span key={category.CategoryId}>{icons}</span>;
    }

    openChallange = () => {
        store.goToAction(`/challenge/${this.props.category.Code}`);
    }

    render() {
        const { category, classes } = this.props;
        const iconClassNames = [classes.icon];
        if (this.state.isOpen)
            iconClassNames.push(classes.iconOpen);

        return (
            <Grid item xs={12} >
                <ListItem onClick={this.openChallange}>
                    <Avatar style={{ backgroundColor: `${getImage(this.props.category.BgImage)}` }} >
                        {category.IsUnlocked ? <Icon fontSize="small" style={{ color: "white" }}>lock_open</Icon> : <Icon fontSize="small" style={{ color: "white" }}>lock</Icon>}
                    </Avatar>
                    <ListItemText>{category.Name}</ListItemText>
                    {this.drawStars()}
                </ListItem>
                <Divider />
            </Grid >
        )
    }

}

export default withStyles(styles)(Category);


