import * as React from 'react';
import { createStyles, withStyles, WithStyles, Table, TableBody, TableRow, TableCell, TableHead, Paper, Card, CardHeader, CardActionArea, CardActions, CardContent, CardMedia, Button, Typography, Theme } from "@material-ui/core";
import { DotFontTypography, DotFontSize } from "./common/DotFontTypography";
import { ILeaderboard } from './api/IApi';
import { observer } from 'mobx-react';
import store from './state-management/Store';

const styles = (theme: Theme) => createStyles({
    root: {
        overflowX: 'auto',
        //border: "none",
        //color: "#d59434",
    },
    paddingSmall: {
        padding: 4
    },
    table: {
        minWidth: 700,
    },
    row: {
        //'&:nth-of-type(odd)': {
        //    backgroundColor: "#1f407a",
        //}        
    },
    first: {
        backgroundColor: `${theme.palette.secondary.dark} !important`,//"#ffad33 !important",
        '& td': {
            color: `${theme.palette.secondary.contrastText} !important`,
            //fontWeight: "bold"
        },
        //'&:nth-of-type(odd)': {
        //    backgroundColor: "#ffc266 !important",
        //}
    },
    paper: {        
        marginBottom: 105
    },
    firstCell: {
        paddingLeft: 16
    }
});

interface IProps extends WithStyles<typeof styles> {
    headerFontSize?: DotFontSize;
    bodyFontSize?: DotFontSize
}


@observer
class Leaderboard extends React.Component<IProps, any>  {

    standings = () => {
        const { classes } = this.props;
        const { Leaderboard } = store;
        if (Leaderboard === undefined) return null;

        return Leaderboard.map((x: ILeaderboard, i) => {
            var rankClass = x.Rank === 1 ? classes.first : "";
            return (
                <TableRow key={i} className={[classes.row, rankClass].join(" ")}>
                    <TableCell className={[classes.root, classes.paddingSmall, classes.firstCell].join(" ")}>
                        <DotFontTypography dotFontSize={this.props.bodyFontSize || 34}>{x.Rank}</DotFontTypography>
                    </TableCell>
                    <TableCell className={[classes.root, classes.paddingSmall].join(" ")}>
                        <DotFontTypography dotFontSize={this.props.bodyFontSize || 34}>{x.Name}</DotFontTypography>
                    </TableCell>
                    <TableCell numeric className={[classes.root, classes.paddingSmall].join(" ")}>
                        <DotFontTypography dotFontSize={this.props.bodyFontSize || 34}>{x.PointsEarned.toLocaleString()}</DotFontTypography>
                    </TableCell>
                </TableRow>
            )
        });
    }

    render() {
        const { classes } = this.props;

        return (
            <>
                <Paper className={classes.paper}>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell className={[classes.paddingSmall, classes.firstCell].join(" ")}><DotFontTypography dotFontColor="inherit" dotFontSize={this.props.bodyFontSize || 30}>#</DotFontTypography></TableCell>
                                <TableCell className={classes.paddingSmall}><DotFontTypography dotFontColor="inherit" dotFontSize={this.props.bodyFontSize || 30}>Name</DotFontTypography></TableCell>
                                <TableCell numeric className={classes.paddingSmall}><DotFontTypography dotFontColor="inherit" dotFontSize={this.props.bodyFontSize || 30}>$$$</DotFontTypography></TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {this.standings()}
                        </TableBody>
                    </Table>

                </Paper>
            </>
        )
    }
}

export default withStyles(styles)(Leaderboard);


