export interface IImageData {
    ImageData: ImageData;
    ImageAsBase64: string;
    Height: number;
    Width: number;
}

export class ImageReader {
    static ReadImageFromFile = (file: File, maxWidth: number): Promise<IImageData> => {
        return new Promise((resolve, reject) => {
            var reader = new FileReader();
            reader.onload = function (readerEvent: any) {
                var image = new Image();
                image.onload = function (imageEvent: any) {
                    var canvas = document.createElement('canvas') as HTMLCanvasElement;
                    canvas.width = maxWidth;
                    canvas.height = canvas.width * (image.height / image.width);
                    var canvasContext = canvas.getContext('2d');
                    if (canvasContext != null) {
                        canvasContext.drawImage(image, 0, 0, canvas.width, canvas.height);
                        var imageData = canvasContext.getImageData(0, 0, canvas.width, canvas.height);
                        var dataUrl = canvas.toDataURL('image/jpeg');
                        var resizedImageAsBase64 = dataUrl.split(";base64,")[1];
                        resolve({ ImageData: imageData, ImageAsBase64: resizedImageAsBase64, Height: canvas.height, Width: canvas.width });
                    }
                }
                if (readerEvent != null) {
                    if (readerEvent.target != null) {
                        image.src = readerEvent.target.result;
                    }
                }
            }
            reader.readAsDataURL(file);
        });
    }
}


