import { BottomNavigation, createStyles, withStyles, Theme } from "@material-ui/core";
import * as React from 'react';
import { BottomNavigationProps } from "@material-ui/core/BottomNavigation";
import BottomNavigationAction, { BottomNavigationActionProps } from "@material-ui/core/BottomNavigationAction";
import store from "../state-management/Store";

const styles = (theme: Theme) => createStyles({
    root: {
        backgroundColor: theme.palette.primary.main
    }
});

const actionStyles = (theme: Theme) => createStyles({
    root: {
        color: theme.palette.primary.light
    },
    selected: {
        color: `${theme.palette.common.white} !important`
    },

})

function btmNav(props: BottomNavigationProps)
{
    return <BottomNavigation {...props} />
}

interface IBottomNavAction extends BottomNavigationActionProps
{
    to: string;
}

function btnNavAction(props: IBottomNavAction)
{
    function goTo()
    {
        store.goToAction(props.to);
    }
    return <BottomNavigationAction {...props} onClick={goTo} />
}

export const StyledBottomNavigation = withStyles(styles)(btmNav);
export const StyledBottomNavigationAction = withStyles(actionStyles)(btnNavAction);



