import { IApi, ILeaderboard, IUserStanding, AllImages } from "./IApi";
import { ModuleOf } from "./ModuleOf";

declare var api: ModuleOf<IApi>;

class ApiController implements IApi {
    GetAllImages(): Promise<AllImages[]> {
        return api.default.prototype.GetAllImages();
    }
    UploadImage(userId: string, imageAsBase64: string): Promise<boolean> {
        return api.default.prototype.UploadImage(userId, imageAsBase64);
    }

    UnlockCategory(userId: string, categoryCode: string): Promise<IUserStanding> {
        return api.default.prototype.UnlockCategory(userId, categoryCode);
    }

    GetUserId(): Promise<string> {
        return api.default.prototype.GetUserId();
    }

    PostAnswer(userId: string, questionId: number, answer: string): Promise<IUserStanding> {
        return api.default.prototype.PostAnswer(userId, questionId, answer);
    }

    GetUserStanding(userId: string): Promise<IUserStanding> {
        return api.default.prototype.GetUserStanding(userId);
    }

    ReadQrCode(imageAsBase64: string): Promise<string> {
        return api.default.prototype.ReadQrCode(imageAsBase64);
    }
}

const apiController = new ApiController();

export default apiController;




